.todo-list {
    text-align: center;
    padding: 2rem;
    background-color: #1a1a1a; 
  }
  
  .todo-input {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .todo-input input {
    padding: 10px;
    width: 300px;
    border: 1px solid #444; 
    background-color: #2b2b2b; 
    color: #e0e0e0; 
    border-radius: 5px;
    font-size: 16px;
  }
  
  .todo-input input::placeholder {
    color: #888; 
  }
  
  .todo-input button {
    margin-left: 10px;
    padding: 10px;
    border: none;
    background-color: #0077b6; 
    color: white;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .todo-input button:hover {
    background-color: #005f89;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    background-color: #333; 
    margin: 10px 0;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #444; 
    border-radius: 5px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5); 
  }
  
  li.completed {
    text-decoration: line-through;
    color: #888; 
  }
  
  .todo-buttons {
    display: flex;
    gap: 10px;
  }
  
  .todo-buttons button {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
 
  .todo-buttons button:first-child {
    background-color: #38b000; 
    color: white;
  }
  
  .todo-buttons button:first-child:hover {
    background-color: #2b7a00;
  }
  
  .todo-buttons button:last-child {
    background-color: #d00000; 
    color: white;
  }
  
  .todo-buttons button:last-child:hover {
    background-color: #a10000; 
  }
  